import React, { useState, useEffect } from 'react';
import { db } from '../firebase'; // Firebase   import
import { collection, getDocs, query, orderBy } from 'firebase/firestore'; // query orderBy 
import haversine from 'haversine-distance'; //    
import useGeolocation from './useGeolocation';
import '../styles/crack.css'; // CSS  import

const NearbyCracks = () => {
  const [cracks, setCracks] = useState([]);
  const [totalCnt, setTotalCnt] = useState(0);
  const [totalData, setTotalData] = useState(0);

  const now_location = useGeolocation();

  useEffect(() => {
    const fetchCracks = async () => {
      try {
        const q = query(collection(db, 'crack_data'), orderBy('cnt', 'desc')); //  orderBy  cnt    
        const crackDataSnapshot = await getDocs(q);
        let nearbyCracks = [];
        let cnt = 0;
        let data_num = 0;

        //    
        crackDataSnapshot.forEach(async (doc) => {
          const data = doc.data();
          const crackLocation = { latitude: data.location[0], longitude: data.location[1] };

          // haversine         
          const distance = haversine(
            { latitude: now_location.latitude, longitude: now_location.longitude }, //  
            crackLocation
          );

          // 10km    
          if (distance <= 10000) { // 10km  
            nearbyCracks.push({ id: doc.id, ...data });
            cnt += data.cnt;
            data_num += 1;
          }
        });

        //  :       
        setCracks(nearbyCracks);
        setTotalCnt(cnt);
        setTotalData(data_num);
      } catch (error) {
        console.error('Error fetching cracks:', error);
      }
    };

    //  (now_location)     
    if (now_location.latitude && now_location.longitude) {
      fetchCracks();
    }
  }, [now_location]);

  return (
    <div className="container">
      <h1>Crack</h1>
      <div className="header">
        <p> : {totalCnt / totalData},   : {totalData}</p>
        <p> {now_location.latitude}</p>
        <p> {now_location.longitude}</p>
      </div>
      <div className="crack-container">
        <h3>    </h3>
        {cracks.map((crack) => (
          <div key={crack.id} className="crack-card">
            <div className="crack-info">
              <p>{crack.time}</p>
              <p>{crack.address}</p>
            </div>
            <img src={crack.img_path} alt="Crack Image" className="crack-image" />
          </div>
        ))}
      </div>
    </div>
  );
};

export default NearbyCracks;
